import React, { useContext, useEffect, useState } from 'react';
import Style from './AllAddresses.module.css';
import AddressContext from '../../Context/AddressContext';
import Spinner from '../Spinner/Spinner';
import { Helmet, HelmetProvider } from 'react-helmet-async';
import axios from 'axios';
import { ThreeDots } from 'react-loader-spinner';

function AllAddresses() {
    const { removeAddress } = useContext(AddressContext);
    const [address, setAddress] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        getUserAddress();
    }, []);

    async function getUserAddress() {
        try {
            const token = localStorage.getItem('userToken'); 
            const { data } = await axios.get(`https://ecommerce.routemisr.com/api/v1/addresses`, {
                headers: { token: token }
            });
            setAddress(data.data);
        } catch (err) {
            console.error(err);
            setError('Failed to load addresses. Please try again later.');
        } finally {
            setLoading(false);
        }
    }

    async function handleRemoveAddress(addressId) {
        try {
            const { data } = await removeAddress(addressId);
            // Update the address state by filtering out the removed address
            setAddress(prevAddresses => prevAddresses.filter(addr => addr._id !== addressId));
        } catch (err) {
            console.error(err);
            setError('Failed to remove address. Please try again later.');
        }
    }

    if (loading) {
        return <Spinner />;
    }

    if (error) {
        return <div className="text-center text-danger">{error}</div>;
    }

    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title>Addresses</title>
                </Helmet>
            </HelmetProvider>
            <div className='mt-24 px-5 border border-slate-200'>
                {address.length > 0 ? (
                    address.map((addr, index) => (
                        <div key={index} className='flex flex-wrap justify-between items-center py-10 border-b'>
                            <div>
                                <p className='text-xl font-bold text-green-600'>Name - <span className='font-medium text-lg'>{addr.name}</span></p>
                                <p className='text-xl font-bold'>Phone - <span className='font-medium text-lg text-gray-950'>{addr.phone}</span></p>
                                <p className='text-xl font-bold'>City - <span className='font-medium text-lg text-gray-950'>{addr.city}</span></p>
                                <p className='text-xl font-bold text-gray-800'>Details - <span className='font-medium text-lg text-gray-800'>{addr.details}</span></p>
                            </div>
                            <div>
                                <button
                                    className='bg-red-600 px-3 py-2 text-lg mt-2 text-white'
                                    onClick={() => handleRemoveAddress(addr._id)}
                                >
                                    Remove Address
                                </button>
                            </div>
                        </div>
                    ))
                ) : (
                    <p>No Addresses</p>
                )}
            </div>
        </>
    );
}

export default AllAddresses;
