import React, { useContext, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import CartContext from '../../Context/CartContext';
import { Helmet, HelmetProvider } from "react-helmet-async";
import axios from 'axios';

function Checkout() {
    const { setItemNums, cartId } = useContext(CartContext);
    const navigate = useNavigate();
    const [successMessage, setSuccessMessage] = useState('');

    const validationSchema = Yup.object({
        details: Yup.string().required('Details is required'),
        city: Yup.string().required('City is required'),
        phone: Yup.string()
            .required('Phone is required')
            .matches(/^01[0125][0-9]{8}$/, 'Phone is invalid'),
    });

    const token = localStorage.getItem('userToken');
    const headers = token ? { token } : {};
    
    const formik = useFormik({
        initialValues: {
            details: '',
            city: '',
            phone: '',
        },
        validationSchema,
        onSubmit: async (values, { setSubmitting, setErrors }) => {
            try {
                const response = await axios.post(`https://ecommerce.routemisr.com/api/v1/orders/${cartId}`, { shippingAddress: values }, { headers });
                console.log('Order response:', response);
                setSuccessMessage('Order created successfully!');
                setItemNums(0);
                // Uncomment to navigate after successful order
                setTimeout(() => {
                    navigate('/allorders');
                }, 2000); 

            } catch (error) {
                console.log('Error processing cash order:');
                setErrors({ submit: error.message || 'Failed to create order. Please try again.' });
            } finally {
                setSubmitting(false);
            }
        }
    });

    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title>Checkout Payment</title>
                </Helmet>
            </HelmetProvider>
            <div className="flex items-center justify-center min-h-screen bg-gray-100">
                <div className="bg-white shadow-lg rounded-lg p-8 max-w-md w-full">
                    <h2 className="text-2xl font-bold text-center text-gray-800 mb-6">Online Payment</h2>
            <form className="space-y-4" onSubmit={formik.handleSubmit}>
                <div className="mb-4">
                    <label htmlFor="details" className="block text-lg font-semibold mb-2">Details</label>
                    <input
                        id="details"
                        type="text"
                        name="details"
                        value={formik.values.details}
                        onBlur={formik.handleBlur}
                        onChange={formik.handleChange}
                        className={`block w-full p-3 border ${formik.touched.details && formik.errors.details ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-indigo-500`}
                        aria-invalid={formik.touched.details && formik.errors.details ? 'true' : 'false'}
                        aria-describedby="detailsError"
                    />
                    {formik.touched.details && formik.errors.details && (
                        <div id="detailsError" className="text-red-500 text-sm">{formik.errors.details}</div>
                    )}
                </div>
                <div className="mb-4">
                    <label htmlFor="city" className="block text-lg font-semibold mb-2">City</label>
                    <input
                        id="city"
                        type="text"
                        name="city"
                        value={formik.values.city}
                        onBlur={formik.handleBlur}
                        onChange={formik.handleChange}
                        className={`block w-full p-3 border ${formik.touched.city && formik.errors.city ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-indigo-500`}
                        aria-invalid={formik.touched.city && formik.errors.city ? 'true' : 'false'}
                        aria-describedby="cityError"
                    />
                    {formik.touched.city && formik.errors.city && (
                        <div id="cityError" className="text-red-500 text-sm">{formik.errors.city}</div>
                    )}
                </div>
                <div className="mb-4">
                    <label htmlFor="phone" className="block text-lg font-semibold mb-2">Phone</label>
                    <input
                        id="phone"
                        type="text"
                        name="phone"
                        value={formik.values.phone}
                        onBlur={formik.handleBlur}
                        onChange={formik.handleChange}
                        className={`block w-full p-3 border ${formik.touched.phone && formik.errors.phone ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-indigo-500`}
                        aria-invalid={formik.touched.phone && formik.errors.phone ? 'true' : 'false'}
                        aria-describedby="phoneError"
                    />
                    {formik.touched.phone && formik.errors.phone && (
                        <div id="phoneError" className="text-red-500 text-sm">{formik.errors.phone}</div>
                    )}
                </div>
                <button
                    className="w-full py-3 px-4 bg-indigo-600 text-white font-semibold rounded-md shadow-md hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                    type="submit"
                    disabled={!(formik.isValid && formik.dirty) || formik.isSubmitting}
                >
                    Cash Order
                </button>
                {formik.errors.submit && (
                    <div className="mt-4 text-red-500" role="alert">
                        Failed to create order. Please try again later.
                    </div>
                )}
                {successMessage && <div className="mt-4 text-green-500">{successMessage}</div>}
            </form>
            </div>
            </div>
        </>
    );
}

export default Checkout;
