import React, { useContext, useEffect, useState } from 'react';
import Style from './Orders.module.css';
import CartContext from '../../Context/CartContext';
import { jwtDecode } from 'jwt-decode';
import {Helmet , HelmetProvider} from "react-helmet-async";

function Orders() {
    const { getUserOrder } = useContext(CartContext);
    const [orders, setOrders] = useState([]);
    const [error, setError] = useState(null);

    useEffect(() => {
        async function fetchOrders() {
            try {
                const token = localStorage.getItem('userToken');
                if (!token) throw new Error('User token is missing');
                const { id } = jwtDecode(token);
                const req = await getUserOrder(id);
                setOrders(req.data);
                console.log(req.data)
            } catch (err) {
                console.error('Error processing Orders:', err);
                setError(err);
            }
        }

        fetchOrders();
    }, [getUserOrder]);

    if (error) {
        return <div>Error: {error.message}</div>;
    }

    return (
        <>
        <HelmetProvider>
           <Helmet>
            <title>Orders </title>
           </Helmet>
        </HelmetProvider>
        <div className="pt-24 mx-auto w-full min-h-96"> 
      <p className='text-3xl'>Your Orders</p>

      {orders.map((order) => {
        return <div key={order.id} >
            <div className="flex flex-wrap items-center">
              <h2 className='font-semibold text-2xl'>#{order.id}</h2>
              <h4 className='font-medium text-xl text-blue-600 mx-4'>Processing</h4>
            </div>
            <p>You have ordered {order.cartItems.length} items.</p>
            <div className="py-5 px-2 flex flex-wrap border-b">
              {order.cartItems.map((item) => {
                return <img key={item._id} src={item.product.imageCover} alt={item.product.title} className='px-2 h-28 w-28'/>
              })}
           
            <hr />
            <div >
              <p className="text-xl font-bold py-1">Total Price - {order.totalOrderPrice} EGP</p>
              <p className="text-base text-indigo-600 py-1">Payment Type - {order.paymentMethodType}</p>
              <p className={`text-base py-1 ${order.isPaid ? 'text-green-500' : 'text-red-500 font-bold'}`}>
                {order.isPaid ? 'Paid' : 'Not Paid'}
              </p>
            </div>
            </div>
        </div>
      })
      }
      </div>  
    </>
    );
}

export default Orders;
