import React, { useContext, useState } from 'react';
import Style from './ChangePassword.module.css';
import * as Yup from 'yup';
import { useFormik } from 'formik';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import {Helmet , HelmetProvider} from "react-helmet-async";
import { ThreeDots } from 'react-loader-spinner';

function ChangePassword() {
    let navigate = useNavigate();
    const [loading, setLoading] = useState(false);
    const [errMsg, setErrMsg] = useState('');
    const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;

    const validationSchema = Yup.object({
        currentPassword: Yup.string()
            .min(8, 'Your password must be at least 8 characters long.')
            .required('Enter a combination of at least 8 characters, letters and punctuation marks.'),
        password: Yup.string()
            .min(8, 'Your password must be at least 8 characters long.')
            .matches(passwordRegex, 'Please choose a more secure password.')
            .required('Enter a combination of at least 8 characters, letters and punctuation marks.'),
        rePassword: Yup.string()
            .oneOf([Yup.ref('password'), null], 'Passwords must match')
            .required('Please re-enter your password.'),
    });

    let formik = useFormik({
        initialValues: {
            currentPassword: '',
            password: '',
            rePassword: '',
        },
        validationSchema,
        onSubmit: async (values) => {
            setLoading(true);
            try {
                const token = localStorage.getItem('userToken'); 
                const { data } = await axios.put(
                    'https://ecommerce.routemisr.com/api/v1/users/changeMyPassword',
                    values,
                    {
                        headers: {
                            token: token
                        }
                    }
                );
                if (data.message === 'success') {
                    setLoading(false);
                    console.log(data);
                    navigate('/');   
                }
            } catch (error) {
                setLoading(false);
                if (error.response && error.response.data) {
                    setErrMsg(error.response.data.errors.msg);
                    console.log('Error Response Data:', error.response.data); 
                } else {
                    console.error('Error:', error);
                }
            }
        },
    });
    // if(loading){
    //     return <Spinner />
    // }

    return (<>
    <HelmetProvider>
        <Helmet>
            <title>Change Password </title>
        </Helmet>
    </HelmetProvider>
    <div className="max-w-lg mx-auto py-10 mt-28">
            <div className="text-center">
                <h2 className="text-2xl font-bold mb-5">Change Password</h2>
            </div>
            <form onSubmit={formik.handleSubmit} className="space-y-6">
            <div>
                <label htmlFor="currentPassword" className="block text-sm font-medium text-gray-700">Current Password</label>
                <input
                    id="currentPassword"
                    type="password"
                    name="currentPassword"
                    value={formik.values.currentPassword}
                    onBlur={formik.handleBlur}
                    onChange={formik.handleChange}
                    className={`mt-1 block w-full px-3 py-2 border ${formik.touched.currentPassword && formik.errors.currentPassword ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500`}
                    />
                {formik.touched.currentPassword && formik.errors.currentPassword && <div className="invalid-feedback">{formik.errors.currentPassword}</div>}
            </div>

            <div>
                <label htmlFor="password" className="block text-sm font-medium text-gray-700">New Password</label>
                <input
                    id="password"
                    type="password"
                    name="password"
                    value={formik.values.password}
                    onBlur={formik.handleBlur}
                    onChange={formik.handleChange}
                    className={`mt-1 block w-full px-3 py-2 border ${formik.touched.password && formik.errors.password ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500`}
                    />
                {formik.touched.password && formik.errors.password && <div className="invalid-feedback">{formik.errors.password}</div>}
            </div>

            <div>
                <label htmlFor="rePassword" className="block text-sm font-medium text-gray-700">Re-enter New Password</label>
                <input
                    id="rePassword"
                    type="password"
                    name="rePassword"
                    value={formik.values.rePassword}
                    onBlur={formik.handleBlur}
                    onChange={formik.handleChange}
                    className={`mt-1 block w-full px-3 py-2 border ${formik.touched.rePassword && formik.errors.rePassword ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500`}
                    />
                {formik.touched.rePassword && formik.errors.rePassword && <div className="invalid-feedback">{formik.errors.rePassword}</div>}
            </div>

            <div className="flex items-center justify-between">
                    {loading ? (
                        <button className="bg-blue-500 text-white p-2 rounded cursor-not-allowed" disabled>
                            <ThreeDots visible={true} height="30" width="30" color="#fff" ariaLabel="three-dots-loading" />
                        </button>
                    ) : (
                        <button
                            type="submit"
                            className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded"
                            disabled={!(formik.isValid && formik.dirty)}
                        >
                            Change Password
                        </button>
                    )}
                </div>
            </form>
        </div></>
    );
}

export default ChangePassword;
