import React, { useContext, useEffect, useState, useCallback } from 'react';
import CartContext from '../../Context/CartContext';
import Spinner from '../Spinner/Spinner';
import { Link } from 'react-router-dom';
import { Helmet, HelmetProvider } from "react-helmet-async";

function Cart() {
    const { getLoggedUserCart, deleteFromCart, updateCartQuantity, clearCart, setItemNums } = useContext(CartContext);
    const [cartDetails, setCartDetails] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    const getCartProducts = useCallback(async () => {
        try {
            const { data } = await getLoggedUserCart();
            if (data?.status === 'success') {
                setCartDetails(data.data);
            } else {
                setCartDetails(null);
            }
        } catch (err) {
            if (err.response && err.response.status === 404) {
                setError('No cart found for the user.');
            }
            setCartDetails(null);
        } finally {
            setLoading(false);
        }
    }, [getLoggedUserCart]);

    const removeItem = useCallback(async (productId) => {
        try {
            const { data } = await deleteFromCart(productId);
            if (data?.status === 'success') {
                setCartDetails(data?.data);
                setItemNums(data?.numOfCartItems);
            } else {
                setError('Failed to remove item. Please try again.');
            }
        } catch (err) {
            console.error('Error removing item:', err);
            setError('Failed to remove item. Please try again.');
        }
    }, [deleteFromCart, setItemNums]);

    const updateQuantity = useCallback(async (productId, count) => {
        if (count === 0) {
            await removeItem(productId);
            return;
        }

        try {
            const { data } = await updateCartQuantity(productId, count);
            if (data?.status === 'success') {
                setCartDetails(data?.data);
            } else {
                setError('Failed to update quantity. Please try again.');
            }
        } catch (err) {
            console.error('Error updating quantity:', err);
            setError('Failed to update quantity. Please try again.');
        }
    }, [removeItem, updateCartQuantity]);

    useEffect(() => {
        getCartProducts();
    }, [getCartProducts]);

    if (loading) {
        return <Spinner />;
    }

    if (error) {
        return <p className="text-center text-red-500">{error}</p>;
    }

    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title>Cart</title>
                </Helmet>
            </HelmetProvider>

            <div className="rounded-lg shadow-lg md:w-3/4 mx-auto my-8 mt-24 min-h-96">
                {cartDetails && cartDetails.products?.length > 0 ? (
                    <>
                        <div className="flex md:flex-col flex-wrap p-4">
                            {cartDetails.products.map((product, index) => (
                                <div className="flex border-b mb-4 pb-4" key={`${product.product._id}-${index}`}>
                                    <div className="w-1/4">
                                        <img className="w-full h-auto object-cover" src={product.product.imageCover} alt={product.product.title} />
                                    </div>
                                    <div className="flex-1 px-4 mt-2">
                                        <h4 className="text-lg font-semibold">{product.product.title.split(' ').slice(0, 3).join(' ')}</h4>
                                        <h5 className="text-md">{product.product.category.name}</h5>
                                        <div className="flex justify-between">
                                            <span>{product.price} EGP</span>
                                            <span className="flex items-center">
                                                <i className="fas fa-star text-yellow-500 mr-1"></i> 
                                                {product.product.ratingsAverage}
                                            </span>
                                        </div>
                                        <p className="font-bold">Total Price: {product.price * product.count} EGP</p>
                                    </div>
                                    <div className="flex flex-col justify-between items-center w-1/4">
                                        <button onClick={() => removeItem(product.product._id)} className="text-red-600 hover:text-red-800">
                                            <i className="fas fa-trash-alt"></i> Remove
                                        </button>
                                        <div className="flex items-center mt-2">
                                            <button onClick={() => updateQuantity(product.product._id, product.count + 1)} className="bg-gray-300 rounded px-2">+</button>
                                            <span className="mx-2">{product.count}</span>
                                            <button onClick={() => updateQuantity(product.product._id, product.count - 1)} className="bg-gray-300 rounded px-2">-</button>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                        <div className="flex flex-wrap justify-center md:justify-between p-4 bg-gray-100">
                            <Link className="bg-blue-600 text-white py-2 px-4 rounded hover:bg-blue-700" to={`/onlinePay`}>
                                Checkout Payment
                            </Link>
                            <Link className="bg-green-600 ms-2 text-white py-2 px-4 rounded hover:bg-green-700" to={`/checkout`}>
                                Create Cash Order
                            </Link>
                            <h5 className="text-lg font-bold text-gray-800 text-center py-2">Total Price: {cartDetails.totalCartPrice} EGP</h5>
                        </div>
                    </>
                ) : (
                    <h2 className="text-center text-lg">No items exist in this cart</h2>
                )}
            </div>
        </>
    );
}

export default Cart;
