import React from 'react';
import Style from './OnlineOfflineComponent.module.css';
import { Online, Offline } from 'react-detect-offline';
import logoOffline from '../../Assets/Images/disconnected.svg';
import logoOnline from '../../Assets/Images/wifi-logo.svg';


function OnlineOfflineComponent() {
    return (
        <div className={`d-flex flex-column`}>
             {/* <Online>
                <div className={`${Style.message} text-main`}>
                    <img src={logoOnline} alt="Logo" width="100" height="100" />
                    <h5>Your internet connection was stored</h5>       
                </div>
            </Online>  */}
            <Offline>
                <div className={`${Style.message} d-flex flex-column text-danger`}>
                   <img className='mx-auto' src={logoOffline} alt="Logo" width="100" height="100" />
                   <h5>No internet connection</h5>
                   <span className='text-muted'>Check your modem of router</span>
                   <span className='text-muted'>Reconnect to Wi-Fi</span>
                </div>
            </Offline>
        </div>
    );
}


export default OnlineOfflineComponent;