import React, { useState } from 'react';
import Style from './Categories.module.css';
import { useQuery } from 'react-query';
import axios from 'axios';
import Spinner from '../Spinner/Spinner';
import { Link } from 'react-router-dom';
import {Helmet , HelmetProvider} from "react-helmet-async";

function Categories() {

  const fetchCategory = () => {
      return axios.get(`https://ecommerce.routemisr.com/api/v1/categories`);
    }
    const {isError ,isLoading ,data} = useQuery('ApiCategories' , 
     () => fetchCategory() ,{ keepPreviousData: true });
 
     if (isLoading) {
         return <Spinner />;
       }
     
       if (isError) {
         return <div>Error loading products. Please try again later.</div>;
       }
     
  
    return <>
    <>
    <HelmetProvider>
    <Helmet>
         <title>Categories</title>
    </Helmet>
    </HelmetProvider>
   
    <div className="grid grid-cols-1 md:grid-cols-3 gap-5 mt-28 mb-14 md:px-20">
       {data?.data.data.map((category) =>(
             <div className="bg-gray-50 text-black p-2" key={category._id}>
              <Link to={`/subcategory/${category._id}`} >
               <div className="cursor-pointer">
                   <img src={category.image} alt='category_Image' className='w-full h-96' />
                   <p className="text-green-500 text-xl font-bold py-2">{category.name}</p>
                   </div>
               </Link>
           </div>
       ))}   
       </div>
    </>
   </>;;
}

export default Categories;