import React, { useContext, useEffect, useState } from 'react';
import WishlistContext from '../../Context/WishlistContext';
import toast from 'react-hot-toast';
import CartContext from '../../Context/CartContext';
import Spinner from '../Spinner/Spinner';
import { Helmet, HelmetProvider } from "react-helmet-async";

function Wishlist() {
    const { getLoggedUserWishlist, removeFromWishlist, setWishlistNum, wishlistNum } = useContext(WishlistContext);
    const [wishlist, setWishlist] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const { addToCart, setItemNums } = useContext(CartContext);

    async function handleAddToCart(productId) {
        try {
            const response = await addToCart(productId);
            if (response.data.status === 'success') {
                toast.success('Product added to cart successfully!', {
                    duration: 2000,
                });
                setItemNums(response.data.numOfCartItems);
            }
        } catch (error) {
            toast.error('Error adding product to cart.');
        }
    }

    async function removeProductFromWishlist(productId) {
        try {
            const { data } = await removeFromWishlist(productId);
            if (data.status === 'success') {
                toast.success('Product removed from wishlist successfully!', {
                    duration: 2000,
                });
                setWishlistNum(wishlistNum - 1);
                getUserWishlist();
            }
        } catch (error) {
            toast.error('Error removing product from wishlist.');
        }
    }

    async function getUserWishlist() {
        try {
            const { data } = await getLoggedUserWishlist();
            setWishlist(data.data);
        } catch (err) {
            console.error(err);
            setError('Failed to load wishlist. Please try again later.');
        } finally {
            setLoading(false);
        }
    }

    useEffect(() => {
        getUserWishlist();
    }, []);

    if (loading) {
        return <Spinner />;
    }

    if (error) {
        return <div className="text-center text-red-500 text-lg">{error}</div>;
    }

    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title>Wishlist</title>
                </Helmet>
            </HelmetProvider>

            <div className="container mx-auto p-4 mt-24">
                {wishlist ? (
                    wishlist.map((item) => (
                        <div
                            className="flex flex-col md:flex-row items-center bg-white shadow-md rounded-lg mb-4 p-4"
                            key={item._id}
                        >
                            <div className="w-full md:w-1/5 flex-shrink-0">
                                <img
                                    className="w-full h-auto rounded"
                                    src={item.imageCover}
                                    alt={item.title}
                                />
                            </div>
                            <div className="w-full md:w-3/5 p-4">
                                <h4 className="text-xl font-semibold mb-2">
                                    {item.title.split(' ').splice(0, 3).join(' ')}
                                </h4>
                                <h5 className="text-gray-600 mb-2">{item.category.name}</h5>
                                <p className="flex justify-between items-center text-lg font-medium">
                                    <span>{item.price} EGP</span>
                                    <span className="flex items-center">
                                        <i className="fas fa-star text-yellow-500 mr-1"></i>
                                        {item.ratingsAverage}
                                    </span>
                                </p>
                            </div>
                            <div className="w-full md:w-1/5 flex flex-col md:flex-row justify-end gap-2">
                                <button
                                    className="bg-indigo-600 hover:bg-indigo-700 text-white font-bold py-2 px-4 rounded transition"
                                    onClick={() => handleAddToCart(item._id)}
                                >
                                    Add to cart
                                </button>
                                <button
                                    className="bg-red-600 hover:bg-red-700 text-white font-bold py-2 px-4 rounded transition"
                                    onClick={() => removeProductFromWishlist(item._id)}
                                >
                                    Remove
                                </button>
                            </div>
                        </div>
                    ))
                ) : (
                    <h2 className="text-center text-lg font-semibold">No items in wishlist</h2>
                )}
            </div>
        </>
    );
}

export default Wishlist;
