import React, { useState } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import {Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { ThreeDots } from 'react-loader-spinner';
import {Helmet , HelmetProvider} from "react-helmet-async";

function Register() {
    const emailRegex = /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/i;
    const phoneRegex = /^((\+[1-9]{1,4}[ \-]*)|(\([0-9]{2,3}\)[ \-]*)|([0-9]{2,4})[ \-]*)*?[0-9]{3,4}?[ \-]*[0-9]{3,4}?$/;
    const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;

    const validationSchema = Yup.object({
        name: Yup.string()
            .min(6, 'Your name must be at least 6 characters long.')
            .max(50, 'Your name must be 50 characters at most.')
            .required('Your name is required.'),
        email: Yup.string()
            .email('Please enter a valid email address.')
            .matches(emailRegex, 'Please enter a valid email address.')
            .required('Your email is required.'),
        phone: Yup.string()
            .matches(phoneRegex, 'Please enter a valid mobile number.')
            .required('Your mobile number is required.'),
        password: Yup.string()
            .min(8, 'Your password must be at least 8 characters long.')
            .matches(passwordRegex, 'Please choose a more secure password.')
            .required('Enter a combination of at least 8 characters, letters and punctuation marks.'),
        rePassword: Yup.string()
            .oneOf([Yup.ref('password'), null], 'Passwords must match.')
            .required('Confirm password is required.')
    });

    const [errMsg, setErrMsg] = useState('');
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();

    const formik = useFormik({
        initialValues: {
            name: '',
            email: '',
            phone: '',
            password: '',
            rePassword: ''
        },
        validationSchema,
        onSubmit: async (values) => {
            setLoading(true);
            try {
                const response = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signup', values);
                if (response.data.message === 'success') {
                    setLoading(false);
                    navigate('/login');
                }
            } catch (error) {
                setLoading(false);
                if (error.response && error.response.data) {
                    setErrMsg(error.response.data.message);
                }
            }
        }
    });

    return (
      <>
    <HelmetProvider>
       <Helmet>
            <title>Register</title>
        </Helmet>
    </HelmetProvider>
     
     <div className="flex items-center justify-center min-h-screen bg-gray-100">
            <div className="bg-white shadow-lg rounded-lg p-8 max-w-md w-full">
                <h2 className="text-2xl font-bold text-center text-gray-800 mb-6">Register Your Account</h2>
                {errMsg && <div className="mb-4 text-red-500 text-center">{errMsg}</div>}
                <form onSubmit={formik.handleSubmit} className="space-y-4">
                    <div>
                        <label htmlFor="name" className="block text-sm font-medium text-gray-700">Name</label>
                        <input
                            id="name"
                            type="text"
                            name="name"
                            value={formik.values.name}
                            onBlur={formik.handleBlur}
                            onChange={formik.handleChange}
                            className={`mt-1 block w-full px-3 py-2 border ${formik.touched.name && formik.errors.name ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500`}
                        />
                        {formik.touched.name && formik.errors.name && <div className="text-red-500 text-sm">{formik.errors.name}</div>}
                    </div>

                    <div>
                        <label htmlFor="email" className="block text-sm font-medium text-gray-700">Email</label>
                        <input
                            id="email"
                            type="email"
                            name="email"
                            value={formik.values.email}
                            onBlur={formik.handleBlur}
                            onChange={formik.handleChange}
                            className={`mt-1 block w-full px-3 py-2 border ${formik.touched.email && formik.errors.email ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500`}
                        />
                        {formik.touched.email && formik.errors.email && <div className="text-red-500 text-sm">{formik.errors.email}</div>}
                    </div>

                    <div>
                        <label htmlFor="phone" className="block text-sm font-medium text-gray-700">Phone</label>
                        <input
                            id="phone"
                            type="tel"
                            name="phone"
                            value={formik.values.phone}
                            onBlur={formik.handleBlur}
                            onChange={formik.handleChange}
                            className={`mt-1 block w-full px-3 py-2 border ${formik.touched.phone && formik.errors.phone ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500`}
                        />
                        {formik.touched.phone && formik.errors.phone && <div className="text-red-500 text-sm">{formik.errors.phone}</div>}
                    </div>

                    <div>
                        <label htmlFor="password" className="block text-sm font-medium text-gray-700">Password</label>
                        <input
                            id="password"
                            type="password"
                            name="password"
                            value={formik.values.password}
                            onBlur={formik.handleBlur}
                            onChange={formik.handleChange}
                            className={`mt-1 block w-full px-3 py-2 border ${formik.touched.password && formik.errors.password ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500`}
                        />
                        {formik.touched.password && formik.errors.password && <div className="text-red-500 text-sm">{formik.errors.password}</div>}
                    </div>

                    <div>
                        <label htmlFor="rePassword" className="block text-sm font-medium text-gray-700">Confirm Password</label>
                        <input
                            id="rePassword"
                            type="password"
                            name="rePassword"
                            value={formik.values.rePassword}
                            onBlur={formik.handleBlur}
                            onChange={formik.handleChange}
                            className={`mt-1 block w-full px-3 py-2 border ${formik.touched.rePassword && formik.errors.rePassword ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500`}
                        />
                        {formik.touched.rePassword && formik.errors.rePassword && <div className="text-red-500 text-sm">{formik.errors.rePassword}</div>}
                    </div>

                    <div>
                        <button
                            type="submit"
                            className="w-full py-2 px-4 bg-indigo-600 text-white font-medium rounded-md shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                            disabled={!(formik.isValid && formik.dirty)}
                        >
                            {loading ? (
                                <ThreeDots height="30" width="30" color="#fff" ariaLabel="loading" />
                            ) : (
                                'Register'
                            )}
                        </button>
                    </div>
                </form>
            </div>
        </div></>
    );
}

export default Register;