import React, { useContext, useState } from 'react';
import Style from './UserDetails.module.css';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import {  useNavigate } from 'react-router-dom';
import axios from 'axios';
import { ThreeDots } from 'react-loader-spinner';
import UserContext from '../../Context/UserContext';
import {Helmet , HelmetProvider} from "react-helmet-async";


function UserDetails() {
    const [errMsg, setErrMsg] = useState('');
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();
   const {setUserData , setUserToken} = useContext(UserContext);

   function logOut() {
    localStorage.removeItem('userToken');
    setUserToken(null);
    navigate('../login');
  }

    const emailRegex = /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/i;
    const phoneRegex = /^((\+[1-9]{1,4}[ \-]*)|(\([0-9]{2,3}\)[ \-]*)|([0-9]{2,4})[ \-]*)*?[0-9]{3,4}?[ \-]*[0-9]{3,4}?$/;
    const validationSchema = Yup.object({
        name: Yup.string()
            .min(6, 'Your name must be at least 6 characters long.')
            .max(50, 'Your name must be 50 characters at most.')
            .required('Your name is required.'),
        email: Yup.string()
            .email('Please enter a valid email address.')
            .matches(emailRegex, 'Please enter a valid email address.')
            .required('Your email is required.'),
        phone: Yup.string()
            .matches(phoneRegex, 'Please enter a valid mobile number.')
            .required('Your mobile number is required.')
    });

    const formik = useFormik({
        initialValues: {
            name: '',
            email: '',
            phone: '',
        },
        validationSchema,
        onSubmit: async (values) => {
            setLoading(true);
            try {
                const token = localStorage.getItem('userToken'); 
                const { data } = await axios.put(
                    'https://ecommerce.routemisr.com/api/v1/users/updateMe/',
                    values,
                    {
                        headers: {
                            token: token
                        }
                    }
                );
                if (data.message === 'success') {
                    setLoading(false);
                    setUserData(data?.data?.user.name);
                    console.log(data);
                    logOut();
                    
               
                }
            } catch (error) {
                setLoading(false);
                if (error.response && error.response.data) {
                    setErrMsg(error.response.data.errors.msg);
                    console.log('Error Response Data:', error.response.data);
                } else {
                    console.error('Error:', error);
                }
            }
        }
    });

    return (<>
<HelmetProvider>
   <Helmet>
        <title>Update Personal Details</title>
    </Helmet>
</HelmetProvider>
<div className="max-w-lg mx-auto py-10 mt-28">
    <div className="text-center">
        <h2 className="text-2xl font-bold mb-5">Update User Details</h2>
    </div>
    <form onSubmit={formik.handleSubmit} className="space-y-6">
    {errMsg && <div className="text-red-600 bg-red-100 p-2 rounded">{errMsg}</div>}
        <div>
            <label htmlFor="name" className="block text-sm font-medium text-gray-700">Name</label>
            <input
                id="name"
                type="text"
                name="name"
                 value={formik.values.name}
                 onBlur={formik.handleBlur}
                 onChange={formik.handleChange}
                className={`mt-1 block w-full px-3 py-2 border ${formik.touched.name && formik.errors.name ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500`}
            />
             {formik.touched.name && formik.errors.name && <div className="text-red-500 text-sm">{formik.errors.name}</div>}
        </div>
        <div>
            <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-2">Email</label>
            <input
                id="email"
                type="email"
                name="email"
                value={formik.values.email}
                onBlur={formik.handleBlur}
                onChange={formik.handleChange}
                className={`w-full p-2 border ${formik.touched.email && formik.errors.email ? 'border-red-500' : 'border-gray-300'} rounded focus:outline-none focus:ring-2 focus:ring-blue-500`}
            />
            {formik.touched.email && formik.errors.email && (
                <div className="text-red-500 text-sm">{formik.errors.email}</div>
            )}
        </div>
        <div>
            <label htmlFor="phone" className="block text-sm font-medium text-gray-700">Phone</label>
            <input
                id="phone"
                type="tel"
                name="phone"
                value={formik.values.phone}
                onBlur={formik.handleBlur}
                onChange={formik.handleChange}
                className={`mt-1 block w-full px-3 py-2 border ${formik.touched.phone && formik.errors.phone ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500`}
             />
             {formik.touched.phone && formik.errors.phone && <div className="text-red-500 text-sm">{formik.errors.phone}</div>}
        </div>
        <div className="flex items-center justify-between">
                    {loading ? (
                        <button className="bg-blue-500 text-white p-2 rounded cursor-not-allowed" disabled>
                            <ThreeDots visible={true} height="30" width="30" color="#fff" ariaLabel="three-dots-loading" />
                        </button>
                    ) : (
                        <>
                            <button
                                type="submit"
                                className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded"
                                disabled={!(formik.isValid && formik.dirty)}
                            >
                                Save Updates
                            </button>
    
                        </>
                    )}
        </div>
            </form>
        </div></>

    );
}

export default UserDetails;
