import React, { useContext, useEffect } from 'react';
import './App.css';
import Layout from './Components/Layout/Layout';
import Login from './Components/Login/Login';
import Register from './Components/Register/Register';
import Brands from './Components/Brands/Brands';
import BrandDetails from './Components/BrandDetails/BrandDetails';
import Cart from './Components/Cart/Cart';
import Categories from './Components/Categories/Categories';
import Home from './Components/Home/Home';
import NotFound from './Components/NotFound/NotFound';
import Orders from './Components/Orders/Orders';
import OnlinePay from './Components/OnlinePay/OnlinePay';
import NewAddress from './Components/NewAddress/NewAddress';
import AllAddresses from './Components/AllAddresses/AllAddresses';
import ProtectedRoute from './Components/ProtectedRoute/ProtectedRoute';
import { createBrowserRouter, createHashRouter, RouterProvider } from 'react-router-dom';
import UserContext from './Context/UserContext';
import ForgotPassword from './Components/ForgotPassword/ForgotPassword';
import ResetPassword from './Components/ResetPassword/ResetPassword';
import ProductDetails from './Components/ProductDetails/ProductDetails';
import UserDetails from './Components/UserDetails/UserDetails';
import Products from './Components/Products/Products';
import Checkout from './Components/Checkout/Checkout';
import Wishlist from './Components/Wishlist/Wishlist';
import SubCategory from './Components/SubCategory/SubCategory';
import SubCategoryProducts from './Components/SubCategoryProducts/SubCategoryProducts';
import ChangePassword from './Components/ChangePassword/ChangePassword';


const routers = createHashRouter([
  {
    path: '/',
    element: <Layout />,
    children: [
      { index: true, element: <ProtectedRoute><Home /></ProtectedRoute> },
      { path: 'brands', element: <ProtectedRoute><Brands /></ProtectedRoute> },
      { path: 'wishlist', element: <ProtectedRoute><Wishlist /></ProtectedRoute> },
      { path: 'brands/brandDetails/:id', element: <ProtectedRoute><BrandDetails /></ProtectedRoute> },
      { path: 'categories', element: <ProtectedRoute><Categories /></ProtectedRoute> },
      { path: 'changeMyPassword', element: <ProtectedRoute><ChangePassword /></ProtectedRoute> },
      { path: 'subcategory/:id', element: <ProtectedRoute><SubCategory /></ProtectedRoute> },
      { path: 'subproducts/:id', element: <ProtectedRoute><SubCategoryProducts /></ProtectedRoute> },
      { path: 'products', element: <ProtectedRoute><Products /></ProtectedRoute> },
      { path: 'cart', element: <ProtectedRoute><Cart /></ProtectedRoute> },
      { path: 'allorders', element: <ProtectedRoute><Orders /></ProtectedRoute> },
      { path: 'checkout', element: <ProtectedRoute><Checkout /></ProtectedRoute> },
      { path: 'onlinePay', element: <ProtectedRoute><OnlinePay /></ProtectedRoute> },
      { path: 'newaddress', element: <ProtectedRoute><NewAddress /></ProtectedRoute> },
      { path: 'alladdresses', element: <ProtectedRoute><AllAddresses /></ProtectedRoute> },
      { path: 'updateme', element: <ProtectedRoute><UserDetails /></ProtectedRoute> },
      { path: 'productDetails/:id', element: <ProtectedRoute><ProductDetails /></ProtectedRoute> },
      { path: '/products/productDetails/:id', element: <ProtectedRoute><ProductDetails /></ProtectedRoute> },
      { path: 'login', element: <Login /> },
      { path: 'register', element: <Register /> },
      { path: 'forgotPassword', element: <ForgotPassword /> },
      { path: 'resetPassword', element: <ResetPassword /> },
      { path: '*', element: <NotFound /> },
    ]
  }
]);

function App() {
  const { setUserToken } = useContext(UserContext);

  useEffect(() => {
    const token = localStorage.getItem('userToken');
    if (token && typeof token === 'string') {
      setUserToken(token);
    } else {
      console.error("Invalid token: must be a string");
      setUserToken("");
    }
  }, [setUserToken]);

  return <RouterProvider router={routers} />
}

export default App;
