import React from 'react';
import "slick-carousel/slick/slick.css"; // Import slick-carousel CSS
import "slick-carousel/slick/slick-theme.css"; // Import slick-carousel theme CSS
import slide2 from '../../Assets/Images/veg.jpg';
import slide3 from '../../Assets/Images/2012.jpg';
import slide4 from '../../Assets/Images/129804.jpg';
import Slider from "react-slick";

function MainSlider() {
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
    };

    return (
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-24 w-full">
            <div className="md:col-span-2">
                <Slider {...settings}>
                    <img className="w-full h-96 object-cover" src={slide4} alt="Slide 1" />
                    <img className="w-full h-96 object-cover" src={slide2} alt="Slide 2" />
                    <img className="w-full h-96 object-cover" src={slide3} alt="Slide 3" />
                </Slider>
            </div>
            <div className="flex flex-col gap-1">
                <img className="w-full h-48 object-cover" src={slide2} alt="Thumbnail 1" />
                <img className="w-full h-48 object-cover" src={slide3} alt="Thumbnail 2" />
            </div>
        </div>
    );
}

export default MainSlider;
