import axios from 'axios';
import React, { createContext} from 'react';

const AddressContext = createContext();
const headers = {
    token: localStorage.getItem('userToken'),
};

const addAddress = (values) =>{
    return axios.post(`https://ecommerce.routemisr.com/api/v1/addresses` , values , {headers})
    .then((response) => {return response})
    .catch((error) => {return error});
}
const getLoggedUserAddress = () =>{
    return axios.get(`https://ecommerce.routemisr.com/api/v1/addresses`  , {headers})
    .then((response) => {return response})
    .catch((error) => {return error});
}

const getSpecificAddress = (addressId) =>{
    return axios.get(`https://ecommerce.routemisr.com/api/v1/addresses/${addressId}`, {headers})
    .then((response) => {return response})
    .catch((error) => {return error});
}
const removeAddress = (addressId) =>{
    return axios.delete(`https://ecommerce.routemisr.com/api/v1/addresses/${addressId}`  , {headers})
    .then((response) => {return response})
    .catch((error) => {return error});
}


export function AddressContextProvider(props) {

    return <AddressContext.Provider value={{ addAddress ,getLoggedUserAddress ,getSpecificAddress , removeAddress}}>
               {props.children}
           </AddressContext.Provider>
}

export default AddressContext;