import React, { useContext, useState } from 'react';
import { useQuery } from 'react-query';
import { Link } from 'react-router-dom';
import axios from 'axios';
import toast from 'react-hot-toast';
import { ThreeDots } from 'react-loader-spinner';
import CartContext from '../../Context/CartContext';
import WishlistContext from '../../Context/WishlistContext';
import Spinner from '../Spinner/Spinner';
import { Helmet, HelmetProvider } from 'react-helmet-async';

function Products() {
    const { addToCart, setItemNums } = useContext(CartContext);
    const { setWishlistNum, wishlistNum, addProductToWishlist } = useContext(WishlistContext);
    const [currentPage, setCurrentPage] = useState(1);
    const [wishlist, setWishlist] = useState([]);

    const fetchProducts = (page = 1) => {
        return axios.get(`https://ecommerce.routemisr.com/api/v1/products?page=${page}`);
    };

    const { isError, isLoading, data, refetch } = useQuery(
        ['ApiProducts', currentPage],
        () => fetchProducts(currentPage),
        { keepPreviousData: true }
    );

    const handlePageChange = (page) => {
        setCurrentPage(page);
        refetch();
    };

    const AddToCart = async (e, productId) => {
        try {
            const response = await addToCart(productId);
            if (response.data.status === 'success') {
                e.target.classList.replace('text-dark', 'text-success');
                toast.success('Product added to cart successfully!', { duration: 2000 });
                setItemNums(response.data.numOfCartItems);
            }
        } catch (error) {
            toast.error('Error adding product to cart.');
            console.error('Error adding product to cart:', error);
        }
    };

    const AddToWishlist = async (e, productId) => {
        try {
            let response = await addProductToWishlist(productId);
            if (response.data.status === 'success') {
                e.target.classList.replace('fa-regular', 'fa-solid');
                setWishlistNum(wishlistNum + 1);
                setWishlist([...wishlist, productId]);
                toast.success('Product added to wishlist successfully!', { duration: 2000 });
            }
        } catch (error) {
            toast.error('Error adding product to wishlist.');
            console.error('Error adding product to wishlist:', error);
        }
    };

    if (isLoading) {
        return <Spinner />;
    }

    if (isError) {
        return <div>Error loading products. Please try again later.</div>;
    }

    const products = data?.data?.data || [];
    const totalPages = data?.data?.metadata?.numberOfPages || 1;

    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title>Products</title>
                </Helmet>
            </HelmetProvider>
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-5 px-10 gap-5 mt-24 mb-10">
                {products.map((item) => (
                    <div className="bg-gray-50 text-black p-2 relative" key={item._id}>
                        <i
                            className={`fa-heart ${wishlist.includes(item._id) ? 'fa-solid' : 'fa-regular'} text-red-500 px-2 absolute top-3 right-3 fa-2x cursor-pointer`}
                            onClick={(e) => AddToWishlist(e, item._id)}
                        ></i>
                        <Link to={`/productDetails/${item._id}`} className="cursor-pointer">
                            <img
                                src={item.imageCover}
                                className="w-full"
                                alt={item.title || item.slug}
                                width="500"
                                height="500"
                            />
                            <p className="text-green-500 text-sm">{item.category.name}</p>
                            <p className="text-lg font-bold py-1">{item.title.split(' ').slice(0, 2).join(' ')}</p>
                            <div className="flex justify-between">
                                <p className="text-lg">{item.price} EGP</p>
                                <p className="text-base">
                                    <i className="fas fa-star text-yellow-400 px-2">{item.ratingsAverage}</i>
                                </p>
                            </div>
                        </Link>
                        <button
                            className="bg-green-500 mt-2 text-white text-lg w-full py-2"
                            onClick={(e) => AddToCart(e, item._id)}
                        >
                            Add To Cart
                        </button>
                    </div>
                ))}
            </div>
            <nav aria-label="Page navigation example" className="flex justify-center my-4">
                <ul className="flex space-x-2">
                    <li className={`page-item ${currentPage === 1 ? 'opacity-50 cursor-not-allowed' : ''}`}>
                        <button
                            className="bg-gray-300 text-gray-800 px-4 py-2 rounded-md hover:bg-gray-400 transition"
                            onClick={() => handlePageChange(currentPage - 1)}
                            disabled={currentPage === 1}
                        >
                            Previous
                        </button>
                    </li>
                    {Array.from({ length: totalPages }, (_, i) => (
                        <li key={i}>
                            <button
                                className={`px-4 py-2 rounded-md transition ${currentPage === i + 1 ? 'bg-blue-500 text-white' : 'bg-gray-200 text-gray-800 hover:bg-gray-300'}`}
                                onClick={() => handlePageChange(i + 1)}
                            >
                                {i + 1}
                            </button>
                        </li>
                    ))}
                    <li className={`page-item ${currentPage === totalPages ? 'opacity-50 cursor-not-allowed' : ''}`}>
                        <button
                            className="bg-gray-300 text-gray-800 px-4 py-2 rounded-md hover:bg-gray-400 transition"
                            onClick={() => handlePageChange(currentPage + 1)}
                            disabled={currentPage === totalPages}
                        >
                            Next
                        </button>
                    </li>
                </ul>
            </nav>
        </>
    );
}

export default Products;
