import React, { useState } from 'react';
import Style from './ResetPassword.module.css';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { Link, useNavigate } from 'react-router-dom';
import { ThreeDots } from 'react-loader-spinner';
import axios from 'axios';
import {Helmet , HelmetProvider} from "react-helmet-async";


function ResetPassword() {
    let [errMsg , setErrMsg] = useState('');
    let[loading , setLoading] = useState(false)
    const emailRegex = /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/i;
    const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;
    
    const validationSchema = Yup.object({
        email: Yup.string()
        .email('Please enter a valid email address.')
        .matches(emailRegex, 'Please enter a valid email address.')
        .required('Your email is required.'),
        newPassword: Yup.string()
        .min(8, 'Your password must be at least 8 characters long.')
        .matches(passwordRegex, 'Please choose a more secure password.')
        .required('Enter a combination of at least 8 characters, letters and punctuation marks.')
    })
    const navigate = useNavigate();
    let formik = useFormik({
        initialValues:{
            email:'',
            newPassword:''
        },validationSchema
        ,onSubmit: async(values)=>{
            setLoading(false);
            try {
                let { data } = await axios.put(`https://ecommerce.routemisr.com/api/v1/auth/resetPassword`, values);
                if (data.token) {
                    setLoading(true);
                    navigate('/login')
                }
            } catch (error) {
                if (error.response && error.response.data) {
                    setErrMsg(error.response.data.message);
                }
            }

        }
    })
 
    return <>
     <HelmetProvider>
       <Helmet>
            <title>Reset Password </title>
        </Helmet>
    </HelmetProvider>
      <div className='max-w-lg mx-auto py-10 mt-28'>
            <div className="text-center">
                <h2 className="text-2xl font-bold mb-5">Reset Password</h2>
            </div>
            <form onSubmit={formik.handleSubmit} className="py-3">
                {errMsg && <div className="alert alert-danger">{errMsg}</div>}
                <div>
                    <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-2">Email</label>
                    <input
                        id="email"
                        type="email"
                        name="email"
                        value={formik.values.email}
                        onBlur={formik.handleBlur}
                        onChange={formik.handleChange}
                        className={`w-full p-2 border ${formik.touched.email && formik.errors.email ? 'border-red-500' : 'border-gray-300'} rounded focus:outline-none focus:ring-2 focus:ring-blue-500`}                    
                        />
                        {formik.touched.email && formik.errors.email && (
                            <div className="text-red-500 text-sm">{formik.errors.email}</div>
                        )}                </div>
                <div>
                    <label htmlFor="newPassword" className="block text-sm font-medium text-gray-700 mb-2">New Password</label>
                    <input
                        id="newPassword"
                        type="password"
                        name="newPassword"
                        value={formik.values.newPassword}
                        onBlur={formik.handleBlur}
                        onChange={formik.handleChange}
                        className={`w-full p-2 border ${formik.touched.newPassword && formik.errors.newPassword ? 'border-red-500' : 'border-gray-300'} rounded focus:outline-none focus:ring-2 focus:ring-blue-500`} 
                    />
                    {formik.touched.newPassword && formik.errors.newPassword && 
                    <div className="text-red-500 text-sm">{formik.errors.newPassword}</div>}
                    </div>
                {loading ? (
                    <button className="btn btn-primary px-3 text-white btn-lg" disabled>
                        <ThreeDots
                            visible={true}
                            height="30"
                            width="30"
                            color="#fff"
                            ariaLabel="three-dots-loading"
                        />
                    </button>
                ) : (
                    <div className='d-flex flex-column align-items-start mt-2'>
                      <button
                        type="submit"
                        className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded"
                        disabled={!(formik.isValid && formik.dirty)}
                    >
                        Update Password
                    </button>
                       </div>
                
                    
                    
                    
                )}
            </form>
            </div>;
    </>;
}

export default ResetPassword;