import React, { useContext } from 'react';
import Style from './Footer.module.css';
import { Link } from 'react-router-dom';
import UserContext from '../../Context/UserContext';

function Footer() {
   let {userToken} = useContext(UserContext);
   
    return <>
    {userToken? <footer class="p-4 bg-green-500 md:p-8 lg:p-10 dark:bg-gray-800 ">
  <div class="mx-auto max-w-screen-xl text-center">
      <Link href="/" class="mb-2 flex justify-center items-center text-2xl font-semibold">
          Fresh Cart    
      </Link>
      <ul class="flex flex-wrap justify-center items-center mb-6 text-gray-900 dark:text-white">
          <li>
              <Link href="/products" class="mr-4 hover:underline md:mr-6 ">Products</Link>
          </li>
          <li>
              <Link href="/categories" class="mr-4 hover:underline md:mr-6">Categories</Link>
          </li>
          <li>
              <Link href="/brands" class="mr-4 hover:underline md:mr-6 ">Brands</Link>
          </li>
          <li>
              <Link href="/orders" class="mr-4 hover:underline md:mr-6">Orders</Link>
          </li>
          <li>
              <Link href="/wishlist" class="mr-4 hover:underline md:mr-6">Wishlist</Link>
          </li>
          <li>
          <Link href="/cart" class="mr-4 hover:underline md:mr-6">Cart</Link>
          </li>
      </ul>
      <span class=" text-gray-500 sm:text-center dark:text-gray-600"> &copy; 2022-2023 <Link href="/" class="hover:underline">Doaa Mahmoud</Link>. All Rights Reserved.</span>
      
      </div>
       </footer>
    :''}
   
     
     </>
}

export default Footer;