import React from 'react';
import axios from 'axios';
import { useQuery } from 'react-query';
import { Link, useParams } from 'react-router-dom';
import { Helmet, HelmetProvider } from 'react-helmet-async';
import Spinner from '../Spinner/Spinner';
import Style from './SubCategory.module.css';

function SubCategory() {
    const { id } = useParams();

    // Function to fetch category details based on the category ID
    const fetchCategoryDetails = async (id) => {
        const { data } = await axios.get(`https://ecommerce.routemisr.com/api/v1/categories/${id}`);
        return data.data; // Assuming the data is structured as response.data.data
    };

    // Function to fetch subcategories based on the category ID
    const fetchSubCategories = async (id) => {
        const { data } = await axios.get(`https://ecommerce.routemisr.com/api/v1/categories/${id}/subcategories`);
        return data.data; // Assuming the data is structured as response.data.data
    };

    // Fetching category details
    const { isLoading: isCategoryLoading, isError: isCategoryError, data: categoryData } = useQuery(
        ['categoryDetails', id],
        () => fetchCategoryDetails(id),
        { keepPreviousData: true }
    );

    // Fetching subcategories
    const { isLoading: isSubCategoryLoading, isError: isSubCategoryError, data: subCategoryData } = useQuery(
        ['subCategories', id],
        () => fetchSubCategories(id),
        { keepPreviousData: true }
    );

    // Handling loading and error states
    if (isCategoryLoading || isSubCategoryLoading) {
        return <Spinner />;
    }

    if (isCategoryError || isSubCategoryError) {
        return <div>Error loading data. Please try again later.</div>;
    }

    const subCategoryList = subCategoryData || []; // Subcategories list
    const categoryDetails = categoryData || {}; // Category details

    return (
        <HelmetProvider>
            <Helmet>
                <title>{categoryDetails.name || 'SubCategories'}</title>
            </Helmet>
            <div className="mx-auto w-full px-5 flex  flex-wrap justify-evenly items-center mt-24 md:mt-28">
                {subCategoryList.length > 0 ? (
                    <>
                        <div className="w-1/2 mx-auto text-center">
                            <img
                                width={500}
                                height={500}
                                src={categoryDetails.image}
                                alt={categoryDetails.name || categoryDetails.slug}
                                className="w-full md:w-1/2 mx-auto"
                            />
                            <p className="text-2xl text-center font-bold py-3">{categoryDetails.name}</p>
                        </div>
                        <div className="grid gap-5 md:w-1/2">
                            {subCategoryList.map((subCat) => (
                                <Link to={`/subproducts/${subCat._id}`} key={subCat._id}>
                                    <div className="bg-green-500 py-2 px-3 text-gray-50">
                                        <h2 className="text-xl font-bold text-center">{subCat.name}</h2>
                                    </div>
                                </Link>
                            ))}
                        </div>
                    </>
                ) : (
                    <div className="px-10 mb-10 min-h-[400px] flex items-center justify-center">
                        <p className="text-2xl">No Subcategories Found</p>
                    </div>
                )}
            </div>
        </HelmetProvider>
    );
}

export default SubCategory;
