import React, { useContext } from 'react';
import { useQuery } from 'react-query';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import Spinner from '../Spinner/Spinner';
import CartContext from '../../Context/CartContext';
import toast from 'react-hot-toast';
import { Helmet, HelmetProvider } from 'react-helmet-async';
import Style from './BrandDetails.module.css';
import Swal from 'sweetalert2';

function BrandDetails() {
  const { addToCart, setItemNums } = useContext(CartContext);
  const { id } = useParams();

  const getBrand = (id) => {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/products?brand=${id}`);
  };

  const { data, isError, isLoading } = useQuery(['ApiGetBrand', id], () => getBrand(id));

  if (isLoading) {
    return <Spinner />;
  }

  if (isError) {
    return <div className="text-center text-2xl">Error loading products. Please try again later.</div>;
  }

  const handleAddToCart = async (productId) => {
    try {
      const response = await addToCart(productId);
      if (response.data.status === 'success') {
        Swal.fire({
          text: 'Product added to cart successfully!',
          icon: 'success',
        });
        setItemNums(response.data.numOfCartItems);
      }
    } catch (error) {
      toast.error('Error adding product to cart.');
      console.error('Error adding product to cart:', error);
    }
  };

  const products = data?.data.data || []; // Extracting products from the response

  return (
    <HelmetProvider>
      <Helmet>
        <title>Brand Details</title>
      </Helmet>
      <div className="px-10 mt-24 mb-10 min-h-[400px] flex items-center justify-center">
        {products.length > 0 ? (
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-5 mx-auto">
            {products.map((product) => (
              <div className="bg-gray-50 text-black p-5 rounded-lg shadow-lg" key={product.id}>
                <img
                  width={500}
                  height={500}
                  src={product.imageCover}
                  className="w-full"
                  alt={product.title || product.slug}
                />
                <p className="text-green-500 text-sm">{product.category?.name}</p>
                <p className="text-lg font-bold py-1">{product.title.split(' ').slice(0, 2).join(' ')}</p>
                <div className="flex justify-between">
                  <p className="text-lg">{product.price} EGP</p>
                  <p className="text-base">
                    <i className="fas fa-star text-yellow-400 px-2" /> {product.ratingsAverage}
                  </p>
                </div>
                <button
                  onClick={() => handleAddToCart(product.id)}
                  className="bg-green-600 py-2 mt-3 text-white w-full rounded-lg"
                >
                  Add Product
                </button>
              </div>
            ))}
          </div>
        ) : (
          <p className="text-center text-2xl">No products found for this brand.</p>
        )}
      </div>
    </HelmetProvider>
  );
}

export default BrandDetails;
