import React, { useContext } from 'react';
import axios from 'axios';
import { useQuery } from 'react-query';
import { Link, useParams } from 'react-router-dom';
import { Helmet, HelmetProvider } from 'react-helmet-async';
import toast from 'react-hot-toast';
import Spinner from '../Spinner/Spinner';
import CartContext from '../../Context/CartContext';
import Style from './SubCategoryProducts.module.css';

function SubCategoryProducts() {
    const { addToCart, setItemNums } = useContext(CartContext);
    const { id } = useParams();

    // Fetch products for the specific subcategory
    const getSubCatProducts = async () => {
        const { data } = await axios.get(`https://ecommerce.routemisr.com/api/v1/products?subcategory=${id}`);
        return data.data; // Assuming data.data contains the list of products
    };

    const { isError, isLoading, data: productList } = useQuery(['ApiProductSubCategories', id], getSubCatProducts, {
        keepPreviousData: true,
    });

    // Display a loading spinner while data is being fetched
    if (isLoading) {
        return <Spinner />;
    }

    // Display an error message if there was an error fetching data
    if (isError) {
        return <div className="text-center text-2xl">Error loading products. Please try again later.</div>;
    }

    const handleAddToCart = async (productId) => {
        try {
            const response = await addToCart(productId);
            if (response.data.status === 'success') {
                toast.success('Product added to cart successfully!', {
                    duration: 2000,
                });
                setItemNums(response.data.numOfCartItems);
            }
        } catch (error) {
            toast.error('Error adding product to cart.');
            console.error('Error adding product to cart:', error);
        }
    };

    return (
        <HelmetProvider>
            <Helmet>
                <title>Sub Category Products</title>
            </Helmet>
            <div className="px-10 mt-24 mb-10 min-h-[400px] flex items-center justify-center">
                {productList && productList.length > 0 ? (
                    <div className="flex items-center justify-center">
                        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-5">
                            {productList.map((product) => (
                                <div className="bg-gray-50 text-black p-2" key={product._id}>
                                    <Link to={`/productDetails/${product._id}`}>
                                        <img
                                            width="500"
                                            height="500"
                                            src={product.imageCover}
                                            className="w-full"
                                            alt={product.title || product.slug}
                                        />
                                        <p className="text-green-500 text-sm">{product.category.name}</p>
                                        <p className="text-lg font-bold py-1">
                                            {product.title.split(' ').slice(0, 2).join(' ')}
                                        </p>
                                        <div className="flex justify-between">
                                            <p className="text-lg">{product.price} EGP</p>
                                            <p className="text-base">
                                                <i className="fas fa-star text-yellow-400 px-2" /> {product.ratingsAverage}
                                            </p>
                                        </div>
                                    </Link>
                                    <button
                                        onClick={() => handleAddToCart(product._id)}
                                        className="bg-green-600 py-2 d-block w-full text-white"
                                    >
                                        Add Product
                                    </button>
                                </div>
                            ))}
                        </div>
                    </div>
                ) : (
                    <p className="text-center text-2xl">No products found for this category.</p>
                )}
            </div>
        </HelmetProvider>
    );
}

export default SubCategoryProducts;
