import React, { useState, useEffect, useContext } from "react";
import 'swiper/css';
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper/modules';
import toast from "react-hot-toast";
import CartContext from "../../Context/CartContext";
import { Link } from "react-router-dom";



const getAllProducts = async () => {
    let request = await fetch(`https://ecommerce.routemisr.com/api/v1/products`);
    let response = await request.json();
    return response.data;
};


function PopularProducts() {
    const [productList, setProductList] = useState([]);
    const {addToCart , setItemNums ,itemNums} = useContext(CartContext);
    useEffect(() => {
        const fetchProducts = async () => {
            const products = await getAllProducts();
            setProductList(products);
        };
        fetchProducts();
    }, []);
    
    const AddToCart = async (productId) => {
        try {
            const response = await addToCart(productId);
            if (response.data.status === 'success') {
                toast.success('Product Added to Cart!', {
                    duration: 2000,
                });
                setItemNums(response.data.numOfCartItems);
                setItemNums(itemNums + 1);
            }
        } catch (error) {
            console.error('Error adding product to cart:', error);
        }
    };
    return (
        <div className="relative "> 
            <div className="absolute top-1/2 left-0 transform-translate-y-1/2 swiper-button-prev z-10 cursor-pointer">
            <i className="fas fa-chevron-left text-xl text-gray-600"></i> 
            </div>
            <div className="absolute top-1/2 right-0 transform -translate-y-1/2 swiper-button-next z-10 cursor-pointer">
            <i class="fas fa-chevron-right text-xl text-gray-600"></i>
            </div>

        <Swiper
        spaceBetween={20}
        slidesPerView={4} // Default for large screens
        breakpoints={{
        0: {  // When the screen width is >= 0px (extra-small screens, mobile)
            slidesPerView: 1,
            spaceBetween: 10,
        },
        640: { // When the screen width is >= 640px (small screens)
            slidesPerView: 2,
            spaceBetween: 10,
        },
        1024: { // When the screen width is >= 1024px (medium screens and up)
            slidesPerView: 4,
            spaceBetween: 20,
        },
        }}
        navigation={{ nextEl: ".swiper-button-next", prevEl: ".swiper-button-prev" }} // Attach buttons
        pagination={{ clickable: true }}
        scrollbar={{ draggable: true }}
        modules={[Navigation, Pagination, Scrollbar, A11y]}
        className="product-slider"
        >
                {productList.map((product) => (
                    <SwiperSlide key={product._id}>
                        <div className="border text-black px-6 rounded-md py-3 text-center mt-10">
                        <Link href={`/products/${product._id}`}>

                            <img width={200} height={200} src={product.imageCover} alt={product.title} className=" h-28 w-44 mx-auto" />
                            <p className="text-green-500 text-sm mt-2">{product.category.name}</p>
                            <p className="text-lg font-bold py-1">{product.title.split(" ").slice(0,2).join(" ")}</p>
                            <p className="text-lg">{product.price} EGP</p>
                        </Link><div className="flex justify-around items-center">
                             <p className="text-base">
                             <i className="fas fa-star text-yellow-400 px-2">{product.ratingsAverage}</i>
                             </p>
                             <button className="bg-green-500 mt-2 text-white text-lg border-green-500 px-3 py-1"
                             onClick={()=>AddToCart(product._id)}>Add</button>
                            </div> 
                      
                        </div>
                    </SwiperSlide>
                ))}
            </Swiper>
        </div>
    );
}

export default PopularProducts;