import axios from 'axios';
import React, { createContext, useEffect, useState } from 'react';

const WishlistContext = createContext();

const headers = {
    token: localStorage.getItem('userToken'),
};

const handleError = (error, message) => {
    console.error(message, error);
    throw error;
};

const getLoggedUserWishlist = () => {
    return axios.get('https://ecommerce.routemisr.com/api/v1/wishlist', { headers })
        .then((response) => response)
        .catch((error) => handleError(error, 'Error showing user wishlist'));
};

const removeFromWishlist = (productId) => {
    return axios.delete(`https://ecommerce.routemisr.com/api/v1/wishlist/${productId}`, { headers })
        .then((response) => response)
        .catch((error) => handleError(error, 'Error showing user wishlist'));
};

const addProductToWishlist = (productId) => {
    return axios.post('https://ecommerce.routemisr.com/api/v1/wishlist', { productId }, { headers })
        .then((response) => response)
        .catch((error) => handleError(error, 'Error adding product to wishlist'));
};

export function WishlistContextProvider(props) {
    const [wishlistNum, setWishlistNum] = useState(0);
    useEffect(() => {
        async function getUserWishlist() {
            try {
                const { data } = await getLoggedUserWishlist();
                setWishlistNum(data?.count);
            } catch (err) {
                console.error('Failed to show wishlist count in navbar:', err);
            }
        }
        getUserWishlist();
    }, [wishlistNum]);

    return (
        <WishlistContext.Provider 
        value={{
         removeFromWishlist,
         getLoggedUserWishlist, 
         addProductToWishlist 
         ,wishlistNum, 
         setWishlistNum
         }}>

            {props.children}
        </WishlistContext.Provider>
    );
}

export default WishlistContext;
