import React, { useState } from 'react';
import Style from './Brands.module.css';
import { useQuery } from 'react-query';
import axios from 'axios';
import Spinner from '../Spinner/Spinner';
import { Link } from 'react-router-dom';
import {Helmet , HelmetProvider} from "react-helmet-async";


function Brands() {
   let[pageNum , setPageNum] = useState(1);

   const fetchBrands = (page = 1) => {
     return axios.get(`https://ecommerce.routemisr.com/api/v1/brands?page=${page}`);
   }
   const {isError ,isLoading ,refetch ,data} = useQuery(['ApiBrands' , pageNum] , 
    () => fetchBrands(pageNum) ,{ keepPreviousData: true });

    if (isLoading) {
        return <Spinner />;
      }
    
      if (isError) {
        return <div>Error loading products. Please try again later.</div>;
      }
    
      const handlePageChange = (page) => {
        setPageNum(page);
        refetch();
      };
    return <>
    <HelmetProvider>
        <Helmet>
         <title>Brands</title>
        </Helmet>
    </HelmetProvider>
    <>
    <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 gap-5 mt-28 mb-14 md:px-20">
        {data?.data.data.map((brand) =>(
                <div className="bg-gray-50 text-black p-2" key={brand._id}>
                <Link to={`brandDetails/${brand._id}`}>
                <div className='brandItem border border-1 px-2'>
                    <img src={brand.image} alt='Brand_Image' className='w-100' />
                    <p className="text-green-500 text-xl font-bold py-2">{brand.name}</p>
                    </div>
                </Link>
            </div>
        ))}
            
        </div>

      <nav aria-label="Page navigation example" className="flex justify-center my-4">
                <ul className="flex space-x-2">
                    <li className={`page-item ${pageNum === 1 ? 'opacity-50 cursor-not-allowed' : ''}`}>
                        <button
                            className="bg-gray-300 text-gray-800 px-4 py-2 rounded-md hover:bg-gray-400 transition"
                            onClick={() => handlePageChange(pageNum - 1)}
                            disabled={pageNum === 1}
                        >
                            Previous
                        </button>
                    </li>
                    {Array.from({ length: data?.data.metadata.numberOfPages }, (_, i) => (
                        <li key={i}>
                            <button
                                className={`px-4 py-2 rounded-md transition ${pageNum === i + 1 ? 'bg-blue-500 text-white' : 'bg-gray-200 text-gray-800 hover:bg-gray-300'}`}
                                onClick={() => handlePageChange(i + 1)}
                            >
                                {i + 1}
                            </button>
                        </li>
                    ))}
                    <li className={`page-item ${pageNum === data?.data.metadata.numberOfPages ? 'opacity-50 cursor-not-allowed' : ''}`}>
                        <button
                            className="bg-gray-300 text-gray-800 px-4 py-2 rounded-md hover:bg-gray-400 transition"
                            onClick={() => handlePageChange(pageNum + 1)}
                            disabled={pageNum === data?.data.metadata.numberOfPages}
                        >
                            Next
                        </button>
                    </li>
                </ul>
            </nav>
     </>
    </>;
}

export default Brands;