import React, { useContext, useState } from 'react';
import { useQuery } from 'react-query';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import Spinner from '../Spinner/Spinner';
import CartContext from '../../Context/CartContext';
import toast from 'react-hot-toast';
import Swal from 'sweetalert2';
import { Helmet } from 'react-helmet';
import WishlistContext from '../../Context/WishlistContext';


function ProductDetails() {
  const params = useParams();
  const { addToCart, setItemNums } = useContext(CartContext);
  const {addProductToWishlist, wishlistNum,setWishlistNum} = useContext(WishlistContext);
  const [mainImage, setMainImage] = useState(null);
  const [wishlist, setWishlist] = useState([]);

  const getProductDetails = (id) => {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/products/${id}`);
  };

  const { isError, isLoading, data } = useQuery('productDetails', () => getProductDetails(params.id), {
    onSuccess: (data) => {
      setMainImage(data.data.data.imageCover); // Set initial main image
    },
  });

  if (isLoading) {
    return <Spinner />;
  }

  if (isError) {
    return <div>Error loading products. Please try again later.</div>;
  }

  const handleAddToCart = async (productId) => {
    try {
      const response = await addToCart(productId);
      if (response.data.status === 'success') {
        toast.success('Product added to cart successfully!', { duration: 2000 });
        // Swal.fire({
        //   text: 'Product added to wishlist successfully!',
        //   icon: 'success',
        // });
        setItemNums(response.data.numOfCartItems);
      }
    } catch (error) {
      toast.error('Error adding product to cart.');
      console.error('Error adding product to cart:', error);
    }
  };

  const handleAddToWishlist = async(productId) => {
    try {
      const response = await addProductToWishlist(productId);
      if (response.data.status === 'success') {
        toast.success('Product added to wishlist successfully!', { duration: 2000 });
        // Swal.fire({
        //   text: 'Product added to wishlist successfully!',
        //   icon: 'success',
        // });
        setWishlistNum(wishlistNum + 1);  
        setWishlist([...wishlist, productId]);
      }
    } catch (error) {
      toast.error('Error adding product to wishlist.');
      console.error('Error adding product to wishlist:', error);
    }
  };

  return (
    <>
      <Helmet>
        <title>Product Details</title>
      </Helmet>
      <div className="md:px-10 mt-28 mb-10 min-h-[400px] flex items-center justify-center">
        {data?.data.data && (
          <div className="mx-auto md:w-4/5 grid grid-cols-1 md:grid-cols-3 gap-4 text-black">
            <div>
              <img
                width="500"
                height="500"
                className="w-full h-96 rounded"
                src={mainImage}
                alt={data.data.data.title || data.data.data.slug}
                id="mainImage"
              />
              <div className="grid grid-cols-4 gap-2 mt-3">
                {data.data.data.images.slice(0, 4).map((img, index) => (
                  <img
                    width="100"
                    height="100"
                    onClick={() => setMainImage(img)}
                    src={img}
                    key={index}
                    alt={data.data.data.title || data.data.data.slug}
                    className="rounded cursor-pointer"
                  />
                ))}
              </div>
            </div>

            <div className="col-span-2 px-5">
              <p className="text-2xl mt-3 font-bold py-2">{data.data.data.title}</p>
              <p className="text-lg">
                <i className="fas fa-star text-yellow-400 px-2" /> {data.data.data.ratingsAverage}
              </p>
              <p className="text-lg mt-2 py-2 text-gray-700">{data.data.data.description}</p>
              <p className="text-lg mt-2 py-2 text-gray-700">{data.data.data.category?.name}</p>
              <p className="text-lg mt-2 py-2 text-gray-700">{data.data.data.price} EGP</p>
              <div className="flex items-center">
                <button
                  className="bg-green-500 mt-2 text-white rounded text-lg w-52 py-2"
                  onClick={() => handleAddToCart(data.data.data._id)}
                >
                  Add To Cart
                </button>
                <div
                  className={`bg-gray-400 px-3 mt-2 cursor-pointer text-red-500 rounded text-lg py-2 ms-3`}
                  onClick={() => handleAddToWishlist(data.data.data._id)}
                >
                  <i className={`fa-heart ${wishlist.includes(data.data.data._id) ? 'fa-solid' : 'fa-regular'} text-lg font-bold`} />
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </>
  );
}

export default ProductDetails;
