import axios from 'axios';
import React, { createContext, useEffect, useState } from 'react';

const CartContext = createContext();
const headers = {
    token: localStorage.getItem('userToken'),
};

const handleError = (error, message) => {
    console.error(message, error);
    throw error;
};

const addToCart = (productId) => {
    return axios.post(`https://ecommerce.routemisr.com/api/v1/cart`, { productId }, { headers })
        .then((response) => response)
        .catch((error) => handleError(error, 'Error adding product to cart:'));
};

const getLoggedUserCart = () => {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/cart`, { headers })
        .then((response) => response)
        .catch((error) => handleError(error));
};

const deleteFromCart = (productId) => {
    return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${productId}`, { headers })
        .then((response) => response)
        .catch((error) => handleError(error, 'Error deleting product from cart:'));
};

const updateCartQuantity = (productId, count) => {
    return axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${productId}`, { count }, { headers })
        .then((response) => response)
        .catch((error) => handleError(error, 'Error updating product quantity:'));
};

const clearCart = () => {
    return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart`, { headers })
        .then((response) => response)
        .catch((error) => handleError(error, 'Error clearing cart:'));
};

const onlinePayment = (cartId, url, values) => {
    return axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${cartId}?url=${url}`, { shippingAddress: values }, { headers })
        .then((response) => response)
        .catch((error) => handleError(error, 'Error processing online payment:'));
};

const getUserOrder = (id) => {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/orders/user/${id}`, { headers })
        .then((response) => response)
        .catch((error) => handleError(error, 'Error processing orders:'));
};

const createCashOrder = (cartId, values) => {
    return axios.post(`https://ecommerce.routemisr.com/api/v1/orders/${cartId}`, { shippingAddress: values }, { headers })
        .then((response) => response)
        .catch((error) => handleError(error, 'Error processing cash Order:'));
};

export function CartContextProvider(props) {
    const [itemNums, setItemNums] = useState(0);
    const [cartId, setCartId] = useState('');

    useEffect(() => {
        async function getCart() {
            try {
                const { data } = await getLoggedUserCart();
                setCartId(data?.data._id);
                setItemNums(data?.numOfCartItems);
            } catch (err) {
                console.error('Failed to fetch cart ID:', err);
            }
        }
        getCart();
    }, []);

    return (
        <CartContext.Provider value={{
            cartId,
            itemNums,
            setItemNums,
            getUserOrder,
            addToCart,
            getLoggedUserCart,
            deleteFromCart,
            updateCartQuantity,
            clearCart,
            onlinePayment,
            createCashOrder,
        }}>
            {props.children}
        </CartContext.Provider>
    );
}

export default CartContext;
