  import React, { useState, useEffect, useContext  } from 'react';
  import Style from './Navbar.module.css';
  import { Link, useNavigate ,useLocation } from 'react-router-dom';
  import UserContext from '../../Context/UserContext';
  import CartContext from '../../Context/CartContext';
  import WishlistContext from '../../Context/WishlistContext';
  
  function Navbar() {
      const [menuOpen, setMenuOpen] = useState(false);
      const { userToken, userData, setUserToken } = useContext(UserContext);
      const { itemNums } = useContext(CartContext);
      const { wishlistNum } = useContext(WishlistContext);
      const location = useLocation();
      const navigate = useNavigate();
      const [dropdownOpen, setDropdownOpen] = useState(false);
      const [dropdownLogoutOpen, setDropdownLogoutOpen] = useState(false);
  
      const pathNames = [
          { Path: "/", Name: "Home" },
          { Path: "/products", Name: "Products" },
          { Path: "/categories", Name: "Categories" },
          { Path: "/brands", Name: "Brands" },
          { Path: "/allorders", Name: "Orders" },
      ];
  
      useEffect(() => {
          const token = localStorage.getItem('userToken');
          if (token) {
              setUserToken(token);
          }
      }, [setUserToken]);
  
      const toggleMenu = () => setMenuOpen(!menuOpen);
      const toggleDropdownInner = () => setDropdownOpen(!dropdownOpen);
      const toggleLogout = () => setDropdownLogoutOpen(!dropdownLogoutOpen);
  
      function logOut() {
          localStorage.removeItem('userToken');
          setUserToken(null);
          navigate('/login'); // Redirect to login page after logout
      }
  
      return (
          <div className="bg-gray-50 text-gray-950 font-mono w-full fixed z-30">
              <nav className="flex flex-wrap justify-around items-center py-2">
                  <Link className="no-underline block text-xl font-bold py-2 px-4" to="/">Fresh Cart</Link>
                  {/* Mobile menu button */}
                  <button
                      onClick={toggleMenu}
                      type="button"
                      className="inline-flex items-center p-2 w-10 h-10 justify-center text-sm text-gray-500 rounded-lg md:hidden hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-200"
                      aria-controls="navbar-default"
                      aria-expanded={menuOpen ? "true" : "false"}
                  >
                      <span className="sr-only">Open main menu</span>
                      <svg className="w-5 h-5" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 17 14">
                          <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M1 1h15M1 7h15M1 13h15" />
                      </svg>
                  </button>
  
                  {/* Menu links */}
                  <div className={`${menuOpen ? "block" : "hidden"} w-full md:block md:w-auto`} id="navbar-default">
                      <ul className="items-center py-2 mb-0 md:border-0 flex flex-col  md:p-0 md:flex-row md:space-x-8">
                          {userToken ? (
                              <>
                                  {pathNames.map((element) => (
                                      <li key={element.Name}>
                                          <Link
                                              className={`no-underline block text-lg font-bold py-2 px-2 ${location.pathname === element.Path ? 'text-green-500' : ''}`}
                                              to={element.Path}
                                              onClick={() => setMenuOpen(false)}
                                          >
                                              {element.Name}
                                          </Link>
                                      </li>
                                  ))}
                                  <li className='md:flex md:ps-12 items-center'>
                                      <div 
                                          className="cursor-pointer px-6 focus:ring-4 focus:outline-none focus:ring-blue-300 font-bold text-lg  py-2.5 text-center inline-flex items-center"
                                          onClick={toggleDropdownInner}
                                      >
                                          Addresses
                                          <svg className="w-2.5 h-2.5 ms-3" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 10 6">
                                              <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="m1 1 4 4 4-4"/>
                                          </svg>
                                      </div>
                                      {dropdownOpen && (
                                          <div className="mb-2 md:absolute z-10 bg-white divide-y divide-gray-100 shadow w-44 top-14">
                                              <ul className="py-2 text-sm text-gray-700">
                                                  <li>
                                                      <Link onClick={()=>{toggleDropdownInner(); setMenuOpen(false)}} to="/newaddress" className="block px-4 py-2 text-base hover:bg-gray-100">Add New Address</Link>
                                                  </li>
                                                  <li>
                                                      <Link onClick={()=>{toggleDropdownInner(); setMenuOpen(false)}} to="/alladdresses" className="block px-4 py-2 text-base hover:bg-gray-100">All Addresses</Link>
                                                  </li>
                                              </ul>
                                          </div>
                                      )}
                                      <Link to='/wishlist'
                                          className={`no-underline block text-lg font-bold py-2 px-6 text-red-400 ${location.pathname === "/wishlist"? 'text-green-500' : ''}`}
                                          onClick={() => setMenuOpen(false)}
                                      >
                                          <div className='relative flex justify-center items-center'>
                                          <i className='fa-regular fa-heart text-red-500 text-2xl'></i>
                                              <div className='bg-red-500 text-white absolute bottom-2 left-7 px-1'>{wishlistNum}</div>
                                          </div>
                                      </Link>
                                      <Link to='/cart'
                                          className={`no-underline block text-lg font-bold py-2 px-4 text-indigo-600 ${location.pathname === "/cart"? 'text-green-500' : ''}`}
                                          onClick={() => setMenuOpen(false)}
                                      >
                                          <div className='relative md:ms-2 mx-auto flex justify-center items-center'>
                                              <svg width="26" height="26" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                  <path d="M7 6V4C7 2.9 7.9 2 9 2H15C16.1 2 17 2.9 17 4V6H20C20.55 6 21 6.45 21 7V21C21 21.55 20.55 22 20 22H4C3.45 22 3 21.55 3 21V7C3 6.45 3.45 6 4 6H7ZM9 6H15V4H9V6ZM5 8V20H19V8H5Z" fill="currentColor" />
                                              </svg>
                                              <div className='bg-indigo-600 text-white absolute bottom-2 left-7 px-1'>{itemNums}</div>
                                          </div>
                                      </Link>
                                  </li>
                                  <li>
                                      <div className="cursor-pointer focus:ring-4 focus:outline-none focus:ring-blue-300 font-bold text-lg py-2.5 text-center inline-flex items-center"
                                          onClick={toggleLogout}>
                                          {userData}
                                          <svg className="w-2.5 h-2.5 ms-3" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 10 6">
                                              <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="m1 1 4 4 4-4"/>
                                          </svg>
                                      </div>
                                      {dropdownLogoutOpen && (
                                          <div className="mb-2 md:absolute z-10 bg-white divide-y divide-gray-100 shadow w-44 top-16 right-2">
                                              <ul className="py-2 text-sm text-gray-700">
                                                  <li>
                                                      <Link onClick={()=>{toggleLogout(); setMenuOpen(false)}} to="/updateme" className="block px-4 py-2 text-base hover:bg-gray-100">Personal Details</Link>
                                                      <Link onClick={()=>{toggleLogout();setMenuOpen(false)}} to="/changeMyPassword" className="block px-4 py-2 text-base hover:bg-gray-100">Change Password</Link>
                                                  </li>
                                                  <li>
                                                      <button className="block px-4 py-2 text-base hover:bg-gray-100 w-full"
                                                       onClick={() => {
                                                          setMenuOpen(false);
                                                          logOut();
                                                          toggleLogout();
                                                      }}>Logout</button>
                                                  </li>
                                              </ul>
                                          </div>
                                      )}
                                  </li>
                              </>
                          ) : (
                              <>
                                  <li>
                                      <Link
                                          className={`no-underline block text-lg font-bold py-2 px-4 ${location.pathname === "/login"? 'text-green-500' : ''}`}
                                          to='/login'
                                          onClick={() => setMenuOpen(false)}
                                      >
                                          Login
                                      </Link>
                                  </li>
                                  <li>
                                      <Link
                                          className={`no-underline block text-lg font-bold py-2 px-4 ${location.pathname === "/register"? 'text-green-500' : ''}`}
                                          to='/register'
                                          onClick={() => setMenuOpen(false)}
                                      >
                                          Register
                                      </Link>
                                  </li>
                              </>
                          )}
                      </ul>
                  </div>
              </nav>
          </div>
      );
  }
  
  export default Navbar;
  