"use client";
import React, { useState, useEffect } from "react";
import 'swiper/css';
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper/modules';
import { Link } from "react-router-dom";


const getAllCategories = async () => {
    const request = await fetch(`https://ecommerce.routemisr.com/api/v1/categories`);
    const response = await request.json();
    return response.data;
};

const FeaturedCategories = () => {
    const [categoryList, setCategoryList] = useState([]);

    useEffect(() => {
        const fetchCategories = async () => {
            const categories = await getAllCategories();
            setCategoryList(categories);
        };
        fetchCategories();
    }, []);

    return (
        <div className="relative">
            <div className="absolute top-1/2 left-0 transform -translate-y-1/2 swiper-btn-previous z-10 cursor-pointer">
               <i className="fas fa-chevron-left text-xl text-gray-600"></i> 
            </div>
            <div className="absolute top-1/2 right-0 transform -translate-y-1/2 swiper-btn-nxt z-10 cursor-pointer">
               <i class="fas fa-chevron-right text-xl text-gray-600"></i>
            </div>

            <Swiper
                spaceBetween={20}
                slidesPerView={4} // Default for large screens
                breakpoints={{
                    0: {  // Extra-small screens, mobile
                        slidesPerView: 1,
                        spaceBetween: 10,
                    },
                    640: { // Small screens
                        slidesPerView: 2,
                        spaceBetween: 10,
                    },
                    1024: { // Medium screens and up
                        slidesPerView: 4,
                        spaceBetween: 20,
                    },
                }}
                navigation={{ nextEl: ".swiper-btn-nxt", prevEl: ".swiper-btn-previous" }} // Attach buttons
                pagination={{ clickable: true }}
                scrollbar={{ draggable: true }}
                modules={[Navigation, Pagination, Scrollbar, A11y]}
                className="product-slider"
            >
                {categoryList.map((category) => (
                    <SwiperSlide key={category._id}>
                        <Link href={`/categories/${category._id}`} key={category._id}>

                        <div className="bg-gray-50 border md:rounded text-black p-2 mx-auto mt-10">
                            <img width={200} height={200} src={category.image} alt={category.name} className="w-32 h-32 mx-auto" />
                            <p className="text-green-500 text-xl font-bold py-2 text-center">{category.name}</p>
                        </div>
                        </Link>
                    </SwiperSlide>
                ))}
            </Swiper>
        </div>
    );
};

export default FeaturedCategories;
