import React, { useContext, useState } from 'react';
import { useFormik } from 'formik';
import CartContext from '../../Context/CartContext';
import * as Yup from 'yup';
import axios from 'axios';
import { Helmet, HelmetProvider } from 'react-helmet-async';
import { ThreeDots } from 'react-loader-spinner';

function OnlinePay() {
    let { cartId } = useContext(CartContext);   
    const [errMsg, setErrMsg] = useState('');
    const [loading, setLoading] = useState(false);

    const validationSchema = Yup.object({
        details: Yup.string().required('Details is required'),
        city: Yup.string().required('City is required'),
        phone: Yup.string()
            .required('Phone is required')
            .matches(/^01[0125][0-9]{8}$/, 'Phone is invalid'),
    });
     
    let formik = useFormik({
        initialValues: {
            details: '',
            city: '',
            phone: '',
        },
        validationSchema,
        onSubmit: async (values, { setErrors }) => {
            try {
                const token = localStorage.getItem('userToken'); 
                const url = 'https://doaamahmoud903.github.io/Eccommerce';
                let { data } = await axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${cartId}?url=${url}`, 
                    { shippingAddress: values },
                    { headers: { token: token } }
                );
                
                if (data?.status === 'success') {
                    console.log(data);
                    console.log(values);
                    console.log('success');
                    window.location.href = data?.session.url;
                }
            } catch (error) {
                if (error.response && error.response.data) {
                    setErrMsg(error.response.data.errors.msg);
                    console.log('Error Response Data:', error.response.data);
                    setErrors({ submit: error.message || 'Failed to create order. Please try again.' });
                } else {
                    console.error('Error:', error);
                }
            }
        }
    });

    return (
        <HelmetProvider>
            <Helmet>
                <title>Online Payment</title>
            </Helmet>
            <div className="flex items-center justify-center min-h-screen bg-gray-100">
                <div className="bg-white shadow-lg rounded-lg p-8 max-w-md w-full">
                    <h2 className="text-2xl font-bold text-center text-gray-800 mb-6">Online Payment</h2>
                    {errMsg && <div className="mb-4 text-red-500 text-center">{errMsg}</div>}
                    <form onSubmit={formik.handleSubmit} className="space-y-4">
                        <div>
                            <label htmlFor="name" className="block text-sm font-medium text-gray-700">Name</label>
                            <input
                                id="name"
                                type="text"
                                name="name"
                                value={formik.values.name}
                                onBlur={formik.handleBlur}
                                onChange={formik.handleChange}
                                className={`mt-1 block w-full px-3 py-2 border ${formik.touched.name && formik.errors.name ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500`}
                            />
                            {formik.touched.name && formik.errors.name && <div className="text-red-500 text-sm">{formik.errors.name}</div>}
                        </div>

                        <div>
                            <label htmlFor="details" className="block text-sm font-medium text-gray-700">Details</label>
                            <textarea
                                id="details"
                                name="details"
                                value={formik.values.details}
                                onBlur={formik.handleBlur}
                                onChange={formik.handleChange}
                                className={`mt-1 block w-full px-3 py-2 border ${formik.touched.details && formik.errors.details ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500`}
                            ></textarea>
                            {formik.touched.details && formik.errors.details && <div className="text-red-500 text-sm">{formik.errors.details}</div>}
                        </div>

                        <div>
                            <label htmlFor="city" className="block text-sm font-medium text-gray-700">City</label>
                            <input
                                id="city"
                                type="text"
                                name="city"
                                value={formik.values.city}
                                onBlur={formik.handleBlur}
                                onChange={formik.handleChange}
                                className={`mt-1 block w-full px-3 py-2 border ${formik.touched.city && formik.errors.city ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500`}
                            />
                            {formik.touched.city && formik.errors.city && <div className="text-red-500 text-sm">{formik.errors.city}</div>}
                        </div>

                        <div>
                            <label htmlFor="phone" className="block text-sm font-medium text-gray-700">Phone</label>
                            <input
                                id="phone"
                                type="tel"
                                name="phone"
                                value={formik.values.phone}
                                onBlur={formik.handleBlur}
                                onChange={formik.handleChange}
                                className={`mt-1 block w-full px-3 py-2 border ${formik.touched.phone && formik.errors.phone ? 'border-red-500' : 'border-gray-300'} rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500`}
                            />
                            {formik.touched.phone && formik.errors.phone && <div className="text-red-500 text-sm">{formik.errors.phone}</div>}
                        </div>

                        <div>
                            <button
                                type="submit"
                                className="w-full py-2 px-4 bg-indigo-600 text-white font-medium rounded-md shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                                disabled={!(formik.isValid && formik.dirty) || loading}
                            >
                                {loading ? (
                                    <div className="flex justify-center">
                                    <ThreeDots height="30" width="30" color="#fff" ariaLabel="loading" />
                                  </div>
                                ) : (
                                    'Online Payment'
                                )}
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </HelmetProvider>
    );
}

export default OnlinePay;
