import Style from './Login.module.css';
import React, { useContext, useState } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { ThreeDots } from 'react-loader-spinner';
import UserContext from '../../Context/UserContext';
import {Helmet , HelmetProvider} from "react-helmet-async";


function Login() {
    const emailRegex = /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/i;
   
    let {setUserToken  } = useContext(UserContext);

    const validationSchema = Yup.object({
        email: Yup.string()
            .email('Please enter a valid email address.')
            .matches(emailRegex, 'Please enter a valid email address.')
            .required('Your email is required.'),
      
        password: Yup.string()
            .min(8, 'Your password must be at least 8 characters long.')
            .required('Enter a combination of at least 8 characters, letters and punctuation marks.'),
    });

    const [errMsg, setErrMsg] = useState('');
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();

    const formik = useFormik({
        initialValues: {
            email: '',
            password: '',
        },
        validationSchema,
        onSubmit: async (values) => {
            setLoading(true);
            try {
                const {data} = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signin', values);
                if (data.message === 'success') {
                    setLoading(false);
                    localStorage.setItem('userToken',data.token);
                    setUserToken(data.token);
                    navigate('/');
                }
            } catch (error) {
                setLoading(false);
                if (error.response && error.response.data) {
                    setErrMsg(error.response.data.message);
                }
            }
        }
    });

    return (
        <>
        <HelmetProvider>
             <Helmet>
                <title>Login | ICart </title>
            </Helmet>
        </HelmetProvider>
        
        <div className="max-w-lg mx-auto py-10 mt-28">
            <div className="text-center">
                <h2 className="text-2xl font-bold mb-5">Login Into Your Account</h2>
            </div>
            <form onSubmit={formik.handleSubmit} className="space-y-6">
                {errMsg && <div className="text-red-600 bg-red-100 p-2 rounded">{errMsg}</div>}
                <div>
                    <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-2">Email</label>
                    <input
                        id="email"
                        type="email"
                        name="email"
                        value={formik.values.email}
                        onBlur={formik.handleBlur}
                        onChange={formik.handleChange}
                        className={`w-full p-2 border ${formik.touched.email && formik.errors.email ? 'border-red-500' : 'border-gray-300'} rounded focus:outline-none focus:ring-2 focus:ring-blue-500`}
                    />
                    {formik.touched.email && formik.errors.email && (
                        <div className="text-red-500 text-sm">{formik.errors.email}</div>
                    )}
                </div>
                <div>
                    <label htmlFor="password" className="block text-sm font-medium text-gray-700 mb-2">Password</label>
                    <input
                        id="password"
                        type="password"
                        name="password"
                        value={formik.values.password}
                        onBlur={formik.handleBlur}
                        onChange={formik.handleChange}
                        className={`w-full p-2 border ${formik.touched.password && formik.errors.password ? 'border-red-500' : 'border-gray-300'} rounded focus:outline-none focus:ring-2 focus:ring-blue-500`}
                    />
                    {formik.touched.password && formik.errors.password && (
                        <div className="text-red-500 text-sm">{formik.errors.password}</div>
                    )}
                </div>
                <div className="flex items-center justify-between">
                    {loading ? (
                        <button className="bg-blue-500 text-white p-2 rounded cursor-not-allowed" disabled>
                            <ThreeDots visible={true} height="30" width="30" color="#fff" ariaLabel="three-dots-loading" />
                        </button>
                    ) : (
                        <>
                            <button
                                type="submit"
                                className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded"
                                disabled={!(formik.isValid && formik.dirty)}
                            >
                                Log in
                            </button>
                            <Link to='/forgotPassword' className="text-blue-500 hover:underline" >Forgotten password?</Link>
                        </>
                    )}
                </div>
                <div className="mt-5">
                    <Link to="/register" className="bg-green-500 hover:bg-green-600 text-white py-2 px-4 rounded text-center block">
                        Create new account
                    </Link>
                </div>
            </form>
        </div></>
    );
}

export default Login;
