import React, { useState } from 'react';
import Style from './ForgotPassword.module.css';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';
import {Helmet , HelmetProvider} from "react-helmet-async";
import { ThreeDots } from 'react-loader-spinner';

function ForgotPassword() {
    const emailRegex = /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/i;
    const [errMsg, setErrMsg] = useState('');
    const [formStatus, setFormStatus] = useState(true);
    const[loading , setLoading] = useState(false)

    const validationSchema = Yup.object({
        email: Yup.string()
            .email('Please enter a valid email address.')
            .matches(emailRegex, 'Please enter a valid email address.')
            .required('Your email is required.')
    });

    const formik = useFormik({
        initialValues: {
            email: ''
        },
        validationSchema,
        onSubmit: async (values) => {
            try {
                let { data } = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/forgotPasswords`, values);
                if (data.statusMsg === 'success') {
                    setFormStatus(false);
                }
            } catch (error) {
                if (error.response && error.response.data) {
                    setErrMsg(error.response.data.message);
                }
            }
        }
    });

    const validationSchemaReset = Yup.object({
        resetCode: Yup.string()
            .required("Security Code is required")
            .matches(/^[0-9]{5,6}$/, 'Security code must be 5-6 digits')
    });
       
    const navigate = useNavigate();
    const formikReset = useFormik({
        initialValues: {
            resetCode: ''
        },
        validationSchema: validationSchemaReset,
        onSubmit: async (values) => {
            try {
                let { data } = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/verifyResetCode`, values);
                if (data.status === 'Success') {
                      console.log("susss")
                      navigate('/resetPassword')
                }
            } catch (error) {
                if (error.response && error.response.data) {
                    setErrMsg(error.response.data.message);
                }
            }
            
        }
    });

    return (
       <>
    <HelmetProvider>
       <Helmet>
            <title>Forgot Password </title>
        </Helmet>
    </HelmetProvider>
       
    <div className='max-w-lg mx-auto py-10 mt-28'>
            <div className="text-center">
                <h2 className="text-2xl font-bold mb-5">Forgot Password</h2>
            </div>
            {formStatus ? (
                <form onSubmit={formik.handleSubmit} className="space-y-6">
                    {errMsg && <div className="text-red-600 bg-red-100 p-2 rounded">{errMsg}</div>}
                    <div>
                        <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-2">Email</label>
                        <input
                            id="email"
                            type="email"
                            name="email"
                            value={formik.values.email}
                            onBlur={formik.handleBlur}
                            onChange={formik.handleChange}
                            className={`w-full p-2 border ${formik.touched.email && formik.errors.email ? 'border-red-500' : 'border-gray-300'} rounded focus:outline-none focus:ring-2 focus:ring-blue-500`}
                        />
                        {formik.touched.email && formik.errors.email && (
                            <div className="text-red-500 text-sm">{formik.errors.email}</div>
                        )}
                    </div>
                    <div className="flex items-center justify-between">
                        {loading ? (
                            <button className="bg-blue-500 text-white p-2 rounded cursor-not-allowed" disabled>
                                <ThreeDots visible={true} height="30" width="30" color="#fff" ariaLabel="three-dots-loading" />
                            </button>
                        ) : (
                            <button
                                type="submit"
                                className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded"
                                disabled={!(formik.isValid && formik.dirty)}
                            >
                                Reset
                            </button>
                        )}
                        <Link
                            type="button"
                             to={"/login"}
                            className="bg-gray-500 hover:bg-gray-600 text-white py-2 px-4 rounded"
                        >
                            Cancel
                        </Link>
                    </div>
                </form>
            ) : (
                <form onSubmit={formikReset.handleSubmit}>
                    {errMsg && <div className="alert alert-danger">{errMsg}</div>}
                    <div className="mb-3">
                        <label htmlFor="resetCode" className="block text-sm font-medium text-gray-700 mb-2">Enter security code</label>
                        <input
                            id="resetCode"
                            type="text"
                            name="resetCode"
                            value={formikReset.values.resetCode}
                            onBlur={formikReset.handleBlur}
                            onChange={formikReset.handleChange}
                            className={`w-full p-2 border ${formikReset.touched.resetCode && formikReset.errors.resetCode ? 'border-red-500' : 'border-gray-300'} rounded focus:outline-none focus:ring-2 focus:ring-blue-500`}
                        />
                        {formikReset.touched.resetCode && formikReset.errors.resetCode && (
                            <div className="text-red-500 text-sm">{formikReset.errors.resetCode}</div>
                        )}
                    </div>
                    <button
                        type="submit"
                        className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded"
                        disabled={!(formikReset.isValid && formikReset.dirty)}
                    >
                        Verify Reset Password
                    </button>
                </form>
            )}
        </div>
        </>
    );
}

export default ForgotPassword;
